html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* //////////////////////////////////////////////////////// */

@font-face {
    font-family: monoton;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

h1 {
	font-family: monoton;
	margin: 10px;
	font-size: 60px;
	color: aliceblue;
	word-spacing: 20px;
	line-height: 1.2em;
	text-align: center;
}

body,
#container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

body {
	gap: 10px;
}

#container {
	margin-top: -10px;
	gap: 20px;
}

#search,
#get,
#tumbler,
img {
	margin: 10px;
}

#search {
	height: 50px;
	font-size: 24px;
	border-radius: 50px;
	background-color: #cccccc;
	text-align: center;
}

#get {
	height: 30px;
	width: fit-content;
	font-size: 18px;
	border-radius: 100px;
	cursor: pointer;
	background-color: #444444;
	color: #ffffff;
}

#search,
#get {
	border: none;
	outline-color: transparent;
}

#get:hover {
	box-shadow: 0px 0px 6px 3px rgba(176, 215, 233, 0.7);
	color: rgb(191, 235, 255);
}

#tumbler {
	width: fit-content;
	height: fit-content;
	background-color: #666666;
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 10px;
	margin-top: -10px;
}

.switch {
	background-color: #8ed2ff;
	width: 60px;
	height: 30px;
	border-radius: 200px;
	cursor: pointer;
	position: relative;
	transition: 0.2s;
}

.switch::before {
	position: absolute;
	content: '';
	background-color: #ffffff;
	width: 24px;
	height: 24px;
	border-radius: 200px;
	margin: 3px;
	transition: 0.2s;
}

#units:checked + .switch {
	background-color: #ff9741;
}

#units:checked + .switch::before {
	transform: translateX(30px);
}

#units {
	display: none;
}

.temp {
	font-size: 30px;
}

.cel {
	color: #8ed2ff;
	font-weight: 900;
}

.fah {
	color: #ff9741;
	font-weight: 900;
}

.inactive {
	color: #444444;
}

.white {
	color: white;
	font-weight: 900;
	text-align: center;
}

