}

.conditions {
	font-size: 20px;
	margin-top: -18px;
}

.city {
	font-size: 25px;
	margin-top: 10px;
}

.description {
	width: 30em;
	margin-top: -8px;
	font-size: 16px;
}

.collapsed {
	display: none;
}

img {
	height: 150px;
	margin-bottom: 0px;
